body {
    overflow-x: hidden;
    max-width: 680px;
    p {
        font-family: "Open Sans", sans-serif;
        font-size: 16px;
        line-height: 1.6;
        color: var(--primary-color);
    }
    span {
        color: var(--primary-color);
    }
    .btn {
        width: 80%;
        min-height: 40px;
        border-radius: 30px;
        font-size: 24px;
        padding: 12px 0;
        background-color: var(--btn-background-color);
        color: var(--btn-text-color);
        border-color: unset;
    }
    h1 {
        font-family: 'Marmelad', sans-serif;
        color: var(--primary-color);
    }
}

:root {
    --animate-duration: 1.5s;
    --animate-delay: 0.5s;
}

