.heart {
  position: fixed;
  z-index: 99999;
  top: -1vh;
  transform: translateY(0);
  animation: fall 3s linear forwards;
  opacity: 0.4;
  color:rgb(255, 255, 255);
  font-weight: 800;
}

@keyframes fall {
  from {
     transform: translateY(0vh) translateX(-20vw); 
  }
  to {
     transform: translateY(105vh) translateX(20vw); 
  }
}