.cmp-couple{
    overflow-x: hidden;
    box-sizing: border-box;
    .title {
        font-family: 'Oooh Baby', cursive;
        font-size: 48px;
    }
    .title-2 {
        font-family: 'Oooh Baby', cursive;
        font-size: 24px;
    }
    .images {
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        position: relative;
        margin: 15px 0;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        img {
            width: 120px;
            opacity: 0;
        }
        .image-left {
            opacity: 1;
            transform: translateX(-100vw);
            animation: slideInFromLeft 4s ease-out infinite;
            animation-delay: 0.5s;
        }
        .image-right {
            opacity: 1;
            transform: translateX(100vw);
            animation: slideInFromRight 4s ease-out infinite;
            animation-delay: 0.5s;
        }
        .image-heart {
            width: 80px; /* hoặc bất kỳ kích thước nào bạn muốn */
            animation: scaleAndFade 4s ease-in-out infinite;
            animation-delay: 0.5s;
        }
    }
}

@keyframes slideInFromLeft {
    0% {
        transform: translateX(-100vw);
        opacity: 0;
    }
    
    20% {
        transform: translateX(0);
        opacity: 1;
    }
    
    80% {
        transform: translateX(0);
        opacity: 1;
    }

    100% {
        transform: translateX(0);
        opacity: 0;
    }
}

@keyframes slideInFromRight {
    0% {
        transform: translateX(100vw);
        opacity: 0;
    }
    
    20% {
        transform: translateX(0);
        opacity: 1;
    }
    
    
    80% {
        transform: translateX(0);
        opacity: 1;
    }

    100% {
        transform: translateX(0);
        opacity: 0;
    }
}

@keyframes scaleAndFade {
    0% {
        transform: scale(0);
        opacity: 1;
    }
  
    20% {
        transform: scale(1);
        opacity: 1;
    }
  
    80% {
        transform: scale(1);
        opacity: 1;
    }
  
    100% {
        transform: scale(1);
        opacity: 0;
    }
}
  