.cmp-slider {
    .swiper-container {
        position: relative;
        overflow-x: hidden;
        .swiper-slide {
            width: fit-content !important;
            height: 150px;
            background-color: #ddd;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 18px;
        }
        .swiper-pagination {
            position: absolute;
            bottom: -20px;
            /* left: 50%;
            transform: translateX(-50%); */
        }
        .swiper-wrapper {
            transition-timing-function: linear !important;
        }
    }
}