.cmp-slide-show-banner {
    height: 100vh;
    span, svg, div {
        color: #373c35;
    }
    .overlay {
        display: none;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgb(0 0 0 / 30%);
    }
    .background {
        background-color: #ffe6e6;
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 1;
        .slides {
            width: 100%;
            height: 100%;
            .slide {
                height: 100%;
                width: 100%;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }

    .content {
        position: relative;
        display: flex;
        justify-self: center;
        height: 100%;
        .time {
            position: absolute;
            bottom: 150px;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    .label--text1 {
        margin-top: 330px;
        font-size: 24px;
        color: #373c35;
    }

    .label--name1 {
        margin-top: 35px;
        font-size: 58px;
        font-family: 'Oooh Baby', cursive;
        color: #373c35;
    }

    .label--delimiter {
        margin: 0px 0px;
        font-size: 30px;
        font-family: 'Oooh Baby', cursive;
        background-color: white;
    }

    .label--name2 {
        margin-top: 0px;
        font-size: 58px;
        font-family: 'Oooh Baby', cursive;
        color: #373c35;
        background-color: white;
    }

    .label--date {
        margin-top: 20px;
        font-size: 24;
    }

    .line {
        width: 80px;
        color: var(--primary-color);
    }

    .btn {
        margin-top: 48px;
        padding: 5px 20px;
        border-radius: 20px;
        height: 48px;
        font-size: 20px;
    }
}