.cmp-wedding-money-box {
    position: relative;
    .line-1 {
        display: none;
        position: absolute;
        border: solid 1px var(--primary-color);
        width: calc(100% - 20px);
        height: calc(100% - 71px);
        left: -1px;
        top: 0px;
        margin: 35px 10px;
        animation: blink-blur 1s ease-in-out infinite;
    }
    .line-2 {
        display: none;
        position: absolute;
        border: solid 1px var(--primary-color);
        width: calc(100% - 30px);
        height: calc(100% - 3px);
        left: 4px;
        top: 0;
        margin: 0px 10px;
        animation: svgAnimation 5s linear infinite;
    }
    .box {
        margin: 40px;
        border: 2px solid var(--primary-color);
        border-radius: 10px;
        padding: 20px 0;
        background-color: white;
        .title {
            span {
                color: var(--primary-color);
                font-size: 18px;
                font-weight: 700;
            }
        }
        .qr {
            margin-top: 20px;
            justify-self: center;
            img {
                width: 35 vw;
                height: auto;

            }
        }
        span {
            color: var(--primary-color);

        }
        .bank-info {
            margin-top: 5px;
            span {
            }
        }
        .bank-account-name {
            margin-top: 5px;
            span {
            }
        }
        .bank-account-no {
            margin-top: 5px;
            /* margin: 5px 0 20px 0; */
            span {
                /* padding-bottom: 20px; */
            }
        }
    }
}