.cmp-introduction {
    .background {
        .label--intention {
            font-size: 30px;
        }
        .animate__bounceInLeft {
            --animate-duration: 3s;
        }
        .animate__bounceInLeft {
            --animate-duration: 1s;
        }
        .thanks {
            padding: 0 15px;
            font-size: 20px;
        }
        .signature {
            justify-content: space-between;
            display: flex;
            padding: 0 45px;
            font-family: 'Oooh Baby', cursive;
            font-size: 48px;
            font-weight: 600;
            .delimiter {
                align-self: center;
                font-size: 30px;
            }
        }
        .parent-info-group {
            justify-content: space-between;
            display: flex;
            padding-left: 20px;
            padding-right: 20px;
            .parent-infos {
                .parent-info {
                    .parent-name {
                        font-weight: 600;
                    }
                    span {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}