.cmp-image-slider {
    .gallery-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.95);
        z-index: 9999;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .main-swiper {
        width: 90%;
        height: 70%;
    }
    
    .main-swiper img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    
    .thumbs-swiper {
        width: 90%;
        height: 60px; /* Giảm chiều cao thumbnail */
        margin-top: 20px;
    }
    
    .thumbs-swiper .swiper-slide {
        width: 60px !important; /* Giảm chiều rộng từng thumbnail */
        height: 100%;
        opacity: 0.5;
        transition: opacity 0.3s;
    }
    
    .thumbs-swiper .swiper-slide-thumb-active {
        opacity: 1;
    }
    
    .thumbs-swiper img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 4px;
        cursor: pointer;
    }

    .swiper-pagination-bullet {
        background: white;
        box-shadow: 0 0 4px rgba(0, 0, 0, 0.8);
        opacity: 1; /* Hiển thị rõ ràng */
        transition: background 0.3s, transform 0.3s;
    }
    .swiper-pagination-bullet-active {
        background: #00bfff; /* Màu xanh dương */
        transform: scale(1.2); /* Phóng to nhẹ cho nổi bật */
    }

    .swiper-button-next, .swiper-button-prev {
        color: white; /* Màu mũi tên */
        text-shadow: 0 0 6px rgba(0, 0, 0, 0.8); /* Bóng đổ đen */
        width: 44px;
        height: 44px;
    }

    .close-button {
        position: absolute;
        top: 20px;
        right: 30px;
        font-size: 2rem;
        color: white;
        background: none;
        border: none;
        cursor: pointer;
        z-index: 10000;
    }

    .view-all-button {
        display: inline-block;
        padding: 14px 24px;
        font-size: 16px;
        font-weight: 600;
        background-color: var(--primary-color);
        color: white;
        border: 1px solid var(--primary-color);
        border-radius: 12px;
        box-shadow: 0 8px 16px rgba(0, 0, 0, 0.25);
        cursor: pointer;
        transition: transform 0.2s ease, box-shadow 0.2s ease;
        -webkit-tap-highlight-color: transparent;
        touch-action: manipulation;
    }
      
    .view-all-button:hover {
        transform: scale(1.05);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
    }
      
    .view-all-button:active {
        transform: scale(0.97);
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    }      
}