.cmp-scroll-to-top {
    position: fixed;
    bottom: 40px;
    right: 10px;
    width: 50px;
    height: 50px;
    border: none;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.9);
    color: var(--primary-color);
    font-size: 24px;
    cursor: pointer;
    z-index: 999;
    box-shadow: 0 4px 8px rgba(0,0,0,0.2);
    transition: opacity 0.3s;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .scroll-to-top:hover {
    background-color: #b89b6d;
  }
  