.cmp-quick-access {
    .background {
        padding: 20px 0;
        background-color: rgba(225, 236, 222, 0.7);
        .button {
            margin-top: 10px;
        }

        .modal-overlay {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.5);
            display: flex;
            justify-content: center;
            align-items: center;
        }
        
        .modal {
            background: white;
            padding: 20px;
            border-radius: 10px;
            text-align: center;
        }
        
        input {
            display: block;
            margin: 10px 0;
            padding: 8px;
            width: 80%;
        }
        
        button {
            margin: 5px;
            padding: 10px;
            cursor: pointer;
            border-radius: 16px;
            background-color: white;
            color: var(--primary-color);
            border: 1px solid var(--primary-color);
            text-transform: uppercase;
            font-family: 'Marmelad', sans-serif;
            font-size: 20px
        }
    }
}