#calendar {
    margin-left: 15px;
    margin-right: 15px;
    .fc {
        font-family: 'Arial', sans-serif;
    }
    
    .fc-daygrid-day {
        background-color: #ffffff; /* Màu nền cho ngày */
    }
    
    .fc-day-today {
        background-color: #8bfff9 !important; /* Highlight ngày hôm nay */
    }
    th .fc-col-header-cell {
        background-color: var(--primary-color);
    }
    .fc-daygrid-day-number {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px; /* Điều chỉnh kích thước chữ */
        font-weight: bold;
        height: 100%; /* Đảm bảo số ngày căn giữa */
    }
      
    .highlight-day {
        border: 2px solid red !important; /* Viền đỏ */
        border-radius: 50% !important; /* Tạo hình tròn */
        text-align: center;
    }

    .fc-daygrid-day-events {
        display: none !important; /* Ẩn toàn bộ sự kiện */
    }

    .fc-daygrid-day-frame:has(a[aria-label="July 16, 2025"]) {
        background-color: var(--background-color-1);
        border: 2px solid var(--primary-color);
        height: 25px;
        a {
            height: 25px;
            word-spacing: 0px;
        }
    }

    .fc-col-header-cell-cushion {
        color: white !important;
    }

    .fc-toolbar-title {
        color: var(--primary-color);
    }

    .fc-button {
        background-color: var(--primary-color);
        .fc-icon {
            color: white;
        }
    }
}