.cmp-text {
  /* margin: 20px 0; */
  position: relative;
  padding: 24px;
  .line-1 {
      position: absolute;
      border: solid var(--primary-color);
      width: calc(100% - 20px);
      height: calc(100% - 13px);
      left: -1px;
      top: 0;
      margin: 5px 10px;
      animation: blink-blur 1s ease-in-out infinite;
  }
  .line-2 {
      position: absolute;
      border: solid var(--primary-color);
      width: calc(100% - 30px);
      height: calc(100% - 3px);
      left: 4px;
      top: 0;
      margin: 0px 10px;
      animation: svgAnimation 5s linear infinite;
  }
  .content {
    color: var(--primary-color);
    font-size: 24px;
  }
  svg {
    position: absolute;
    left: 0;
    top: 0;
    .line {
      stroke-width: 2px;
      fill: transparent;
      stroke-dasharray: 100; /* Match stroke length */
      stroke-dashoffset: 20000; /* Initially hidden */
    }
  }
  svg:nth-child(1){
    width: calc(100% - 20px);
    height: 100%;
    left: 10px;
    .line {
      stroke: rgba(24, 82, 4, 1);
      animation: svgAnimation 5s linear infinite;
    }
  }
  svg:nth-child(2){
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    top: 5px;
    left: 5px;
    .line {
      stroke: rgba(144, 51, 255, 1);
      animation: svgAnimation 5s linear infinite;
    }
  }
}
  
  @keyframes svgAnimation {
    0% { stroke-dashoffset: 500; }
    100% { stroke-dashoffset: 0; }
  }

  .example-5 {
    position: relative;
  }
  
  .example-5 svg {
    position: absolute;
  }